import React, { Component } from 'react';
import { Container, Row, Col } from 'reactstrap';
import Layout from './Layout';
import { PlayProvider} from '../Providers';

class Docs extends Component {
  render() {
    return (
      <PlayProvider>
        <Layout>
          <Container fluid>
            <Row className="mt-5">
              <Col md={{ size: 10, offset: 1 }}>
                <h1>Docs</h1>
              </Col>
            </Row>
          </Container>
        </Layout>
      </PlayProvider>

    );
  }
}

export default Docs;