import React, { Component } from 'react';
import { withRouter, Link } from 'react-router-dom';
import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
    Button
 } from 'reactstrap';
import "./Styles.css";
import {PlayContext} from '../Providers';


class Layout extends Component {
    constructor(props) {
        super(props);
    
        this.toggle = this.toggle.bind(this);
        this.state = {
          isOpen: false,
          isLogin: false
        };
    }

    static contextType = PlayContext; //React 16.6 support

   componentDidMount() {

  }

    componentDidUpdate(prevProps, prevState) {

     }
    

    toggle() {
      this.setState({
        isOpen: !this.state.isOpen
      });
    }

  render() {
    return (
        <div>
          <Navbar color="dark" dark expand="md">
          <NavbarBrand href="/">PWA-APP</NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <NavLink tag={Link} to="/">首頁</NavLink>
              </NavItem>
              <NavItem>
                <NavLink tag={Link} to="/about">關於</NavLink>
              </NavItem>
              <NavItem>
                <NavLink tag={Link} to="/docs">文件</NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
            <div className="main">
                {this.props.children}
            </div>
            <div>
              <p className="footer text-center">Copyright ©2019 Tim </p>
            </div>
        </div>

    );
  }
}

export default withRouter(Layout);